s from bets shall be transferred to the Authorities.\n\n5. Naijalottery has the right to deny the creation of a new betting account.\n\n6. The application for the opening of an account is submitted by completing in the Registration section. Kindly bear in mind that the money in your betting accounts neither includes nor can claim interests.\n\n7. Any suspicion related to the eventually illegitimate carrying out of a game, or a sport venue may lead Naijalottery to withdraw the payments until the case has been fully investigated. Any such investigation will be carried out in cooperation with the State, or Independent Authorities. Any bets made for such venues may be declared void and the wagers may be returned.\n\n8. The results of the sports venues are the ones stated upon their ratification by the official competent organs unless it is otherwise specified. Any change of the result, after the ratification, shall have absolutely no impact on the betting.\n\n9. Naijalottery has the right to deny or cancel, in whole or in part, any betting.\n\n10. A betting slip sent for confirmation shall be considered valid only after it has been confirmed and only for the\n \nAmount of money that it has been confirmed.\n\n11. All the clients shall always ensure:\n- That submit their true residence address\n- That they register in their own name\n- That they fill-in their true e-mail address\n- That they fill-in their true telephone and cell phone numbers\n- That they hold one and only one betting account\n- That they do not act for somebody else's interest, or on somebody else's account\n- That they do not deposit money that comes from criminal activities\n- That they do not use their account for money 'laundering'.\n \n12. Self-exclusion:  Each player has the right to request his exclusion from selected betting products (or to all of them) for a specific period of time of 6, 12, 18, 24 and 36 months accordingly, avoiding him to place any bets during this period. During this period of time, you cannot request to revoke the restriction and you have not the right to open a new account. In case of identifying a new account, the company will immediately close this account. Also, it would be wise to unsubscribe from possible company's subscriptions or Social media.\n\nIn order a player to self-exclude himself, he should download and print the player's self-exclusion application form, please click here. The form must be completed and signed by the applicant, as well as a colour photograph of the face of the applicant (passport type) should be attached to the correspondent frame. The signed application, along with a colour copy of the applicant's Passport or ID card, should be scanned and sent via email to the email address: support@Bettabet.co.ng. The effective exclusion date is set the date of the receipt of the answer from the company, to the filled email address, stating the successful execution of the application. It should be understood that the self-exclusion application is irrevocable.\n\nOther restrictions placed by the player:\nAny registered client may send a written, or an electronic notification:\n\n - setting Limits for deposit per year/month/week/day/hour\n - setting Limits for betting/ loss amounts.\nAny client who has set a limit may modify the above limits always upon a written, or an electronic notification sent to our company. The activation of requested changes, such as the increase of the limits, will be made 7 days after the request has been received.\nthe activation of requested changes, such as the decrease of the limits will be made directly, upon the reception of the request.\nWe will accept no wagers exceeding the limits that have been set, by players subject to the above restrictions.\n\n13. No confirmed bet can be modified, or cancelled, by the player. The company has the right to cancel accepted bets until one hour before the beginning of the game, without justification, only upon notification of the client, by an e-mail sent to the address he/ she stated in the registration form, or by contacting him/ her by any available\n \nCommunication channel.\n\n14. In case the date or the hour of a venue is postponed, the slips that were submitted before the initial date and hour of the venue will be considered valid. The bets that were submitted after the initial date and hour of the venue will be considered void. If the game is included in a slip, together with other games, this particular game will be given 'one' (1).\n\n15. The odds of the venues may present variations during the venues. The applying odds will be those indicated on the slip, upon the acceptance of the bet.\n\n16. In case of a visible mistake in the published odds and cash-out system, the company has the right to correct the faulty odds and to simultaneously cancel the slips containing this particular game, even if the result of the game has already been shaped. In case of a single result, the wager is returned to the player. In case of multiple bets, the game will be given 'one' (1). Also, if there is a suspicion of delay in changing markets in live betting and illegal betting, our Company reserves the right to cancel all bets placed in this match.\n\n17. The minimum bet for betting is NGN 100 or any equivalent amount in any other currency. The minimum bet for Pools Is NGN20\n\n18. The maximum profit per betting, is a thousand times the betting wager, there is no maximum bet amount.\nThe maximum profit is a thousand times the betting wager.\n\n20. All the profits will be credited in the Naijalottery client's betting account.\n\n21. Winnings from settled bets/wagers are added to the balance of your betting account. Any funds/winnings credited to an account in error are not available for use, and International Lottery and Gaming Ltd reserves the right to void any transactions involving such funds and/or withdraw the relevant amount from your account and/or reverse the transaction, either at the time or retrospectively.\n\n22. If a game is interrupted, before the 90th minute (including stoppage time), or postponed and not finished in a period of 24 hours, the bet will be considered void and the odds will be equal to 'one' (1) for all betting options. The bets that have been completed before the interruption of the game will be considered active.\n\n23. Any game postponed (Football, Basketball, Tennis, Handball, Formula 1, Motorcycles) for more than 24 hours will be considered void, if it is not finished or not replayed. The odds will be equal to 'one' (1).\n\n24. The dates, home teams and times displayed for every match are for reference only. The actual game date, time and court for every match is determined from the official game regulator. In case of date misplacement of a match (from Naijalottery), then all bets which were accepted before the official match start will be considered valid, while all bets which were placed after the official match start or with a wrong home team, will be considered void and the\n \nOdd shall be equal to 1.00.\n\n25. Any bet that has not been confirmed before the first game will be considered void. A bet will be considered won if it appears on the betting list of the user's account.\n\n26. Naijalottery is in no case responsible for problems related to problematic PCs, telecommunication lines or internet connections, nor for play methods implemented by clients who are not approved by our company.\n\n27. In spite of the fact that all the necessary measures are taken, in order to protect the Software against viruses, we cannot and will not guarantee you that your PC will not be affected, with such thing resulting to the interruption of our services. The services will be rendered again, within the reasonable time period required for the restoration of any damage you may suffer, after the interruption of our services.\n\n28. By accessing the Internet Site Addresses of Naijalottery you accept the terms and requirements of this agreement. If you do not agree with the above terms and requirements, please, do not access our internet site addresses.\n\n29. As a requirement for the use of the Internet Site Addresses of Naijalottery, one must guarantee that the internet site addresses will not be used for illegal purposes, or purposes violating the above terms and requirements.\n\n30. If any part of this agreement becomes void, or does not conform to the applying laws, including, without limitation, the guarantees and the liabilities stipulated above, an applying term, matching the intentions of the initial purpose will prevail over such void or non-conforming part of the agreement, while the rest of the agreement will remain in force.\n\n31. The Terms and conditions apply to all types of betting, unless differently stated to the individual explanatory categorization field.\n\n32. On the provision of any rights not included hereto.\n\n33. This agreement is subject to the Rules of International Lottery and Gaming Ltd.\n")]),_c('h3',{staticClass:"pt-5 pb-3"},[_vm._v("DEPOSIT / WITHDRAWAL / MONEY LAUNDERING")]),_c('p',{staticClass:"px-md-5 px-3"},[_vm._v("\nNaijalottery has the right to cancel or declare as void (in its sole discretion) transactions of what kind so ever that show irregularities, in particular, irregularities of the following nature:\n\n1) The name and address of the player do not match the name and address associated with the credit card, or other mode of payment, used by the player/customer.\n2) Any irregularity which may negatively affect the processing of payment transactions and/or which gives rise to the suspicion of a violation of the General Terms and Conditions.\n3) Naijalottery takes any form of fraudulent activity of players/customer in connection with services and/or products offered by Naijalottery very seriously. Any fraudulent activity, as determined at Naijalottery ´s sole discretion, is strictly prohibited. Fraudulent activity may include, but is not limited to, stolen credit cards, transfer 