nd more in order to request withdrawal). The bets with odds less than 1,80 (per event) will not count for the deposit rollover. (The bet/bet slip that contains selections of Draw No Bet/ Handicap / Cashout and Postponed matches do not count for deposit rollover).\n\nTherefore, if in Naijalottery discretion, there is suspicion of abuse (for instance, where a deposited amount has not been used for an appropriate level of gameplay and the user then makes a withdrawal request in relation to that deposited amount), Naijalottery reserves the right in its absolute discretion to cancel the respective deposit(s) in part or in full, to retract any costs that may have resulted in conjunction therewith and to close the Naijalottery account indefinitely.\n\nNaijalottery further reserves the right to request and obtain satisfactory proof of deposit and additional copies of personal identification in such instances prior to processing any withdrawal request, the processing of such request to be entirely at Naijalottery 's discretion\n\n")])])])])])}]

